<template>
  <div v-zinx v-if="pwdShow">
    <div class="wrapper-mask" @click="onClose"></div>
    <div class="forgot_password">
      <div ref="forgotPwdRef" class="small_box dialog_radius">
        <!-- 头部 -->
        <div class="forgot_title">
          <span class="forgot_title_content">{{ forgotTitle }}</span>
          <icon-font
            class="forgot_title_icon cursor_point"
            name="icon-Icon_Cloose"
            @click="onClose"
            :size="variables.forgot_password_icon_size"
          ></icon-font>
        </div>
        <!-- 输入框 -->
        <v-form ref="form" lazy-validation class="forgot_form">
          <!-- 邮箱修改密码 -->
          <template v-if="sign == 'email'">
            <!-- 确认邮箱 -->
            <template v-if="!emailPwd">
              <!-- 邮箱 -->
              <v-text-field
                class="field_text input_radius"
                v-model="params.email"
                :rules="verificationEmail"
                :placeholder="$t('UI.cashout_1_bank_12')"
                :height="variables.forgot_password_form_field_height"
                color="t_border_color"
                background-color="t_input_bg"
                required
                clearable
                single-line
                solo
                flat
                hide-details="auto"
                outlined
                :validate-on-blur="true"
              >
                <template slot="prepend-inner">
                  <div class="prepend d-flex align-center">
                    <icon-font
                      class="forgot_title_icon"
                      name="icon-Icon_InBox"
                      @click="onClose"
                      :size="variables.forgot_password_form_prepend_icon_size"
                    ></icon-font>
                  </div>
                </template>
              </v-text-field>
            </template>
            <!-- 重置邮箱密码 -->
            <template v-else>
              <!-- 验证码 -->
              <v-text-field
                class="field_text input_radius"
                v-model="params.code"
                :rules="verificationCode"
                :placeholder="$t('UI_LOGIN_promoCode')"
                :height="variables.forgot_password_form_field_height"
                type="number"
                color="t_border_color"
                background-color="t_input_bg"
                required
                clearable
                single-line
                solo
                flat
                hide-details="auto"
                outlined
                :validate-on-blur="true"
              >
                <template slot="prepend-inner">
                  <div class="prepend d-flex align-center">
                    <icon-font
                      class="forgot_title_icon"
                      name="icon-Icon_shield"
                      @click="onClose"
                      :size="variables.forgot_password_form_prepend_icon_size"
                    ></icon-font>
                  </div>
                </template>
              </v-text-field>
              <!-- 一次输入密码 -->
              <v-text-field
                class="field_text input_radius"
                v-model="params.pwd"
                :rules="verificationPwd"
                :placeholder="$t('UI.Login_0_main_2')"
                :height="variables.forgot_password_form_field_height"
                @click:append="pwdState = !pwdState"
                :type="!pwdState ? 'password' : 'text'"
                color="t_border_color"
                background-color="t_input_bg"
                required
                single-line
                solo
                flat
                hide-details="auto"
                outlined
                :validate-on-blur="true"
              >
                <template slot="prepend-inner">
                  <div class="prepend d-flex align-center">
                    <icon-font
                      class="forgot_title_icon"
                      name="icon-Icon_Lock"
                      @click="onClose"
                      :size="variables.forgot_password_form_prepend_icon_size"
                    ></icon-font>
                  </div>
                </template>
              </v-text-field>
              <!-- 二次输入密码 -->
              <v-text-field
                class="field_text input_radius"
                v-model="params.pwd2"
                :rules="verificationPwd_2"
                :placeholder="$t('UI_Reset_pwd')"
                :height="variables.forgot_password_form_field_height"
                @click:append="pwdStateTow = !pwdStateTow"
                :type="!pwdStateTow ? 'password' : 'text'"
                color="t_border_color"
                background-color="t_input_bg"
                required
                single-line
                solo
                flat
                hide-details="auto"
                outlined
                :validate-on-blur="true"
              >
                <template slot="prepend-inner">
                  <div class="prepend d-flex align-center">
                    <icon-font
                      class="forgot_title_icon"
                      name="icon-Icon_Lock"
                      @click="onClose"
                      :size="variables.forgot_password_form_prepend_icon_size"
                    ></icon-font>
                  </div>
                </template>
              </v-text-field>
            </template>
          </template>
          <!-- 手机号修改密码 -->
          <template v-else-if="sign == 'tel'">
            <!-- 手机号输入 -->
            <v-text-field
              class="field_text input_radius"
              v-model="params.tel"
              :rules="verificationTel"
              :placeholder="$t('UI.Login_0_main_1')"
              :height="variables.forgot_password_form_field_height"
              color="t_border_color"
              background-color="t_input_bg"
              required
              type="number"
              hide-details="auto"
              clearable
              single-line
              solo
              flat
              outlined
              :validate-on-blur="true"
            >
              <template slot="prepend-inner">
                <user-select
                  @change="par => (params.area = par.code)"
                  :value="params.area"
                  :userIcon="phonePlaces[0].flag"
                  outlined
                  :list="phonePlaces"
                >
                </user-select>
              </template>
            </v-text-field>
            <!-- 一次输入密码 -->
            <v-text-field
              class="field_text input_radius"
              v-model="params.pwd"
              :rules="verificationPwd"
              :placeholder="$t('UI.Login_0_main_2')"
              :height="variables.forgot_password_form_field_height"
              @click:append="pwdState = !pwdState"
              :type="!pwdState ? 'password' : 'text'"
              color="t_border_color"
              background-color="t_input_bg"
              required
              single-line
              solo
              flat
              hide-details="auto"
              outlined
              :validate-on-blur="true"
            >
              <template slot="prepend-inner">
                <div class="prepend d-flex align-center">
                  <icon-font
                    class="forgot_title_icon"
                    name="icon-Icon_Lock"
                    @click="onClose"
                    :size="variables.forgot_password_form_prepend_icon_size"
                  ></icon-font>
                </div>
              </template>
            </v-text-field>
            <!-- 验证码 -->
            <v-text-field
              class="field_text input_radius"
              v-model="params.code"
              :rules="verificationCode"
              color="t_border_color"
              background-color="t_input_bg"
              :placeholder="$t('UI.Login_0_main_10')"
              :height="variables.forgot_password_form_field_height"
              required
              type="number"
              hide-details="auto"
              :validate-on-blur="true"
              outlined
              single-line
              solo
              flat
            >
              <template slot="append">
                <v-btn
                  :height="variables.forgot_password_form_text_code_height"
                  :width="variables.forgot_password_form_text_code_width"
                  :disabled="!params.tel"
                  class="text_code btn_radius"
                  color="t_primary_dark_color"
                  @click="getCode"
                >
                  <div>{{ countDown }}</div>
                </v-btn>
              </template>
            </v-text-field>
          </template>
        </v-form>
        <!-- 按钮 -->
        <div class="forgot_btn">
          <!-- 邮箱修改密码按钮 -->
          <div v-if="!emailPwd" class="modify_btn">
            <v-btn
              width="100%"
              :height="variables.forgot_password_footer_btn_height"
              class="btn_radius rt"
              color="t_primary_dark_color"
              @click="onConfirmEmail"
            >
              <div class="forgot_btn_text">
                {{ $t('UI_Reset_Password') }}
              </div>
            </v-btn>
          </div>
          <!-- 邮箱确认按钮 -->
          <div v-else class="confirm_btn">
            <v-btn
              :height="variables.forgot_password_footer_btn_height"
              class="btn_radius lt"
              :width="variables.forgot_password_footer_back_btn_width"
              color="t_btn_color_4"
              shaped
              @click="emailClose"
            >
              <icon-font
                class="forgot_title_icon"
                name="icon-Icon_Back"
                @click="onClose"
                :size="variables.forgot_password_footer_back_btn_icon_size"
              ></icon-font>
            </v-btn>
            <v-btn
              :height="variables.forgot_password_footer_btn_height"
              color="t_primary_dark_color"
              class="confirm_btn_rigth btn_radius"
              @click="onSubmit"
            >
              <div class="forgot_btn_text">
                {{ $t('UI_Reset_pwd') }}
              </div>
            </v-btn>
          </div>
        </div>
      </div>
    </div>
    <template v-if="recaptchaState">
      <vue-recaptcha-verify
        ref="recaptchaRef"
        :titleText="$t('UI.Login_Regist_Tips')"
        :contentText="$t('UI.Login_Regist_Content_Text')"
        v-model="recaptchaState"
        @onSuccess="onSuccess"
        @onBack="onBack"
      ></vue-recaptcha-verify>
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import loadIcon from '@/assets/icons'
import { isEmail, lodash } from '@/utils/helper'
import { getEmailcode, getResetPwd, get_code } from '@/api/modules/login'
import UserSelect from '@/components/ui/user-select'
import VueRecaptchaVerify from '@/components/ui/vue-recaptcha-verify/index'
import { POP_TIME } from '@/api/setting'
import variables from '@style/components/game-container.login.modules.scss'
export default {
  components: {
    UserSelect,
    VueRecaptchaVerify
  },
  data() {
    return {
      variables,
      pwdShow: false, // 弹窗显示
      emailPwd: false, // 邮箱二次修改密码显示
      forgotTitle: '', // 弹窗标题
      sign: 'email', // 标识是邮箱还是手机号 email:邮箱 tel:手机号
      params: {
        // 参数
        tel: '', // 手机号
        area: '', // 区号
        email: '', // 邮箱
        code: '', // 验证码
        pwd: '', // 一次输入密码
        pwd2: '' // 二次输入密码
      },
      pwdState: false, // 密码是否查看
      pwdStateTow: false, // 密码2是否查看
      countDown: this.$t('UI.Login_0_main_10'), // 倒计时
      timerInterval: null,
      recaptchaState: false, // 谷歌图片开关
      recaptcha: '', // 谷歌图片验证返回值
      currentRecaptcha: 1, // 1 提交邮箱重置按钮 2 手机号，获取验证码 3提交手机号重置按钮
      verify_img_params: '' // 图形自验验证码加密code
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig', 'deviceid']),
    ...mapState('source', ['phonePlaces']),
    ...mapGetters('base', ['getRootParams']),
    // 手机号验证
    verificationTel() {
      return [v => !!v || this.$t('UI_reset_phone')]
    },
    // 验证码验证
    verificationCode() {
      return [v => !!v || this.$t('UI_reset_qwd_code')]
    },
    // 邮箱验证
    verificationEmail() {
      return [v => !!v || this.$t('UI_LOGIN_emailEmpty'), v => (v && isEmail(v)) || this.$t('UI_LOGIN_emailFormat')]
    },
    // 一次输入密码验证
    verificationPwd() {
      return [
        v => !!v || this.$t('UI_LOGIN_passwordEmpty'),
        v => this.$u.isPwdCheck(v) || this.$t('UI.Pwd_Format_Check_Text')
      ]
    },
    // 二次输入密码验证
    verificationPwd_2() {
      return [
        v => !!v || this.$t('UI_LOGIN_resetPwdEmpty'),
        v => (v && this.params.pwd == v) || this.$t('UI_LOGIN_resetPwdDif')
      ]
    }
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    /**
     * 打开弹窗
     * @param {String} sign 标识是邮箱登录还是手机号登录 email:邮箱 tel:手机号
     * @param {Object} par 事先输入 input 的参数
     */
    open({ sign = 'email', par = {} }) {
      return new Promise((resolve, reject) => {
        this.resolve = resolve
        this.reject = reject

        // 事先登录的时候是否填入了参数 tel: 手机号  area: 手机号区号 email: 邮件
        const { tel = '', area = '', email = '' } = par
        this.params.tel = tel
        this.params.area = area
        this.params.email = email
        this.params.code = ''
        this.params.pwd = ''
        this.params.pwd2 = ''

        this.sign = sign
        this.emailPwd = false
        this.forgotTitle = this.$t('UI.Login_0_main_3')
        this.pwdShow = true
        this.pwdState = false
        this.pwdStateTow = false

        this.$nextTick(() => {
          const forgotPwdRef = this.$refs['forgotPwdRef']
          if (forgotPwdRef) this.$anim.insert(forgotPwdRef)
        })
      })
    },
    // 关闭弹窗
    onClose() {
      this.$nextTick(() => {
        const forgotPwdRef = this.$refs['forgotPwdRef']
        if (forgotPwdRef) this.$anim.close(forgotPwdRef)

        setTimeout(() => {
          this.resolve('cancel')
          this.pwdShow = false
        }, POP_TIME)
      })
    },
    emailClose() {
      this.emailPwd = false
    },
    // 手机号，获取验证码
    async getCode() {
      if (!this.params.tel) return
      if (this.codeValue > 0) return
      const { language } = this.getRootParams
      const { tel = '', area = '' } = this.params
      let areaNew = area.replace('+', '')
      areaNew = lodash.isBlank(areaNew) ? this.phonePlaces[0]?.code.replace('+', '') : areaNew

      const par = { area: areaNew, tel: areaNew + '' + tel, type: 2, language, device_id: this.deviceid }
      if (this.recaptchaState) {
        par.recaptcha = this.recaptcha
      }

      // 图形自验验证码加密串
      if (this.verify_img_params) par.verify_img_params = this.verify_img_params
      this.verify_img_params = ''

      const res = await get_code(par).catch(e => e)
      const { code, msg = '', data } = res
      if (res && res.code == 0) {
        this.codeValue = data || 60
        this.countDown = this.codeValue + 's'
        this.timerInterval = setInterval(() => {
          if (this.codeValue > 0) {
            this.countDown = this.codeValue + 's'
            this.codeValue--
          } else {
            this.countDown = this.$t('UI.Login_0_main_10')
            clearInterval(this.timerInterval)
            this.timerInterval = null
          }
        }, 1000)
      }

      // 谷歌验证码弹起来
      if (res && res.code == 300) {
        this.currentRecaptcha = 2
        this.recaptchaState = true
        return
      }
      // code == 301 图形验证自验验证码
      if (res && res.code == 301) {
        const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = data || {}
        const resDrag = await this.$pop
          .imgDrag({ baseSrc, checkCode, checkSrc })
          .show()
          .catch(e => e)
        if (resDrag == 'cancel') return

        const { encrypted = '' } = resDrag
        this.verify_img_params = encrypted
        this.getCode()
        return
      }

      // 关闭谷歌图片验证弹窗
      this.recaptchaState = false

      if (code != 0) this.$pop.message().show({ content: msg })

      this.$pop.message().show({ content: msg })
    },
    // 确认邮箱是否正确按钮 || 提交手机号重置按钮
    async onConfirmEmail() {
      const { email = '', tel = '', area = '', code = '', pwd = '' } = this.params
      // 验证邮箱
      if (this.sign == 'email') {
        if (!email) return this.$pop.message().show({ content: this.$t('UI_LOGIN_emailEmpty') })
        if (!isEmail(email)) return this.$pop.message().show({ content: this.$t('UI_LOGIN_emailFormat') })

        const parEmail = {
          token: this.user.token,
          email,
          language: this.languageType,
          type: 2,
          device_id: this.deviceid
        }
        if (this.recaptchaState) {
          parEmail.recaptcha = this.recaptcha
        }

        // 图形自验验证码加密串
        if (this.verify_img_params) parEmail.verify_img_params = this.verify_img_params
        this.verify_img_params = ''

        this.$pop.loading().show()
        const res = await getEmailcode(parEmail).catch(e => e)
        this.$pop.loading().hide()
        // 谷歌验证码弹起来
        if (res && res.code == 300) {
          this.currentRecaptcha = 3
          this.recaptchaState = true
          return
        }
        // code == 301 图形验证自验验证码
        if (res && res.code == 301) {
          const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = res.data || {}
          const resDrag = await this.$pop
            .imgDrag({ baseSrc, checkCode, checkSrc })
            .show()
            .catch(e => e)
          if (resDrag == 'cancel') return

          const { encrypted = '' } = resDrag
          this.verify_img_params = encrypted
          this.onConfirmEmail()
          return
        }

        // 关闭谷歌图片验证弹窗
        this.recaptchaState = false

        this.$pop.message().show({ content: res.msg || res.message || '' })
        this.forgotTitle = this.$t('UI_Reset_pwd')
        this.emailPwd = true
      }

      // 手机号重置密码
      if (this.sign == 'tel') {
        const valid = this.$refs.form.validate()
        if (!valid) return

        let areaNew = area.replace('+', '')
        areaNew = this.$u.lodash.isBlank(areaNew) ? this.phonePlaces[0].value.replace('+', '') : areaNew

        const params = {
          type: 1,
          login_type: 1,
          area: areaNew,
          tel: areaNew + '' + tel,
          pwd,
          code,
          language: this.languageType,
          device_id: this.deviceid
        }
        if (this.recaptchaState) {
          params.recaptcha = this.recaptcha
        }

        // 图形自验验证码加密串
        if (this.verify_img_params) params.verify_img_params = this.verify_img_params
        this.verify_img_params = ''

        this.$pop.loading().show()
        const res = await getResetPwd(params).catch(e => e)
        this.$pop.loading().hide()
        // 谷歌验证码弹起来
        if (res && res.code == 300) {
          this.currentRecaptcha = 3
          this.recaptchaState = true
          return
        }
        // code == 301 图形验证自验验证码
        if (res && res.code == 301) {
          const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = res.data || {}
          const resDrag = await this.$pop
            .imgDrag({ baseSrc, checkCode, checkSrc })
            .show()
            .catch(e => e)
          if (resDrag == 'cancel') return

          const { encrypted = '' } = resDrag
          this.verify_img_params = encrypted
          this.onConfirmEmail()
          return
        }

        // 关闭谷歌图片验证弹窗
        this.recaptchaState = false

        if (res.code != 0) return this.$pop.message().show({ content: res.msg || res.message || '' })

        this.$pop.message().show({ content: res.msg || res.message || '' })
        // 更新user
        const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
        const token_new = resData.token || ''
        const user_new = Object.assign({}, this.user, { token: token_new })
        this.$store.commit('base/SET_USER', user_new)
        // 请求用户数据
        this.$store.dispatch('base/refreshAccount')

        this.pwdShow = false
        setTimeout(() => {
          this.resolve('ok')
        }, 500)
      }
    },
    // 提交邮箱重置按钮
    async onSubmit() {
      const valid = this.$refs.form.validate()
      if (!valid) return

      const { email = '', code = '', pwd = '' } = this.params

      const par = {
        type: 2,
        login_type: 2,
        pwd,
        code,
        account: email,
        language: this.languageType,
        device_id: this.deviceid
      }
      if (this.recaptchaState) {
        par.recaptcha = this.recaptcha
      }

      // 图形自验验证码加密串
      if (this.verify_img_params) par.verify_img_params = this.verify_img_params
      this.verify_img_params = ''

      this.$pop.loading().show()
      const res = await getResetPwd(par).catch(e => e)
      this.$pop.loading().hide()
      // 谷歌验证码弹起来
      if (res && res.code == 300) {
        this.currentRecaptcha = 1
        this.recaptchaState = true
        return
      }
      // code == 301 图形验证自验验证码
      if (res && res.code == 301) {
        const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = res.data || {}
        const resDrag = await this.$pop
          .imgDrag({ baseSrc, checkCode, checkSrc })
          .show()
          .catch(e => e)
        if (resDrag == 'cancel') return

        const { encrypted = '' } = resDrag
        this.verify_img_params = encrypted
        this.onSubmit()
        return
      }

      // 关闭谷歌图片验证弹窗
      this.recaptchaState = false

      if (res.code != 0) return this.$pop.message().show({ content: res.msg || res.message || '' })

      this.$pop.message().show({ content: res.msg || res.message || '' })
      // 更新user
      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      const token_new = resData.token || ''
      const user_new = Object.assign({}, this.user, { token: token_new })
      this.$store.commit('base/SET_USER', user_new)
      // 请求用户数据
      this.$store.dispatch('base/refreshAccount')

      this.pwdShow = false
      setTimeout(() => {
        this.resolve('ok')
      }, 500)
    },
    // 谷歌验证码成功后返回数据
    onSuccess(res) {
      this.recaptcha = res
      if (this.currentRecaptcha == 1) {
        // 提交邮箱重置按钮
        this.onSubmit()
      }
      if (this.currentRecaptcha == 2) {
        //  手机号，获取验证码
        this.getCode()
      }
      if (this.currentRecaptcha == 3) {
        // 提交手机号重置按钮
        this.onConfirmEmail()
      }
    },
    onBack() {
      this.recaptchaState = false
    }
  },
  beforeDestroy() {
    clearInterval(this.timerInterval)
    this.timerInterval = null
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.login.modules.scss';

// 针对移动端样式
.forgot_password {
  position: fixed;
  width: $forgot_password_app_width;
  left: 4%;
  top: $forgot_password_app_position_top;
  .small_box {
    width: 100%;
    background: $forgot_password_bg;
    padding: $forgot_password_padding;
    overflow: hidden;
    .forgot_title {
      @include setBox(row, space-between, center);
      .forgot_title_content {
        color: $forgot_password_title_color;
        font-size: $forgot_password_title_font_size;
        font-weight: $forgot_password_title_font_weight;
      }
      .forgot_title_icon {
        color: $forgot_password_icon_color;
      }
    }
    .forgot_form {
      margin: $forgot_password_form_margin;
      .field_text {
        padding: $forgot_password_form_padding;
        font-size: $forgot_password_form_font_size;
        .prepend {
          margin: $forgot_password_form_prepend_margin;
        }
        .text_code {
          div {
            min-width: 60px;
          }
        }
      }
      .field_text.v-text-field--outlined ::v-deep fieldset {
        border-width: 1px;
      }
      .field_text.v-application .error--text fieldset {
        border-color: $forgot_password_error_title_color !important;
        color: $forgot_password_error_title_color !important;
        caret-color: $forgot_password_error_title_color !important;
      }
      /*text-field 获得焦点后边框宽度*/
      .field_text.v-text-field--outlined.v-input--is-focused ::v-deep fieldset {
        border-width: 1px;
      }
      .field_text ::v-deep .lock_icon {
        font-size: $forgot_password_lock_icon_font_size;
      }
    }
    .forgot_btn {
      margin: $forgot_password_footer_margin;
      .modify_btn {
        @include setBox(row, center, center);
      }
      .confirm_btn {
        width: 100%;
        @include setBox(row, space-between, center);
        .lt {
          flex-shrink: 0;
          flex-grow: 0;
          margin: $forgot_password_footer_back_btn_margin;
        }
        .rt {
          flex-shrink: 1;
          flex-grow: 1;
        }
        .confirm_btn_rigth {
          flex: 1;
        }
        .v-btn:not(.v-btn--round).v-size--default {
          height: $forgot_password_confirm_btn_item_height;
          min-width: $forgot_password_confirm_btn_item_min_width;
          padding: $forgot_password_confirm_btn_item_min_pa;
        }
      }
      .forgot_btn_text {
        font-size: $forgot_password_footer_btn_font_size;
        font-weight: $forgot_password_footer_btn_font_weight;
      }
    }
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .forgot_password {
    position: fixed;
    width: $forgot_password_pc_width;
    left: 50%;
    top: $forgot_password_pc_position_top;
    transform: translateX(-50%);
  }
}
</style>
