<template>
  <div>
    <cpf-regist
      v-if="systemConfig.reg_cpf_control_switch == 1 && !isLogin && !isCpfVerify"
      @onCpfSubmit="onCpfSubmit"
    />
    <template v-else>
      <v-form ref="form" v-model="valid" lazy-validation>
        <!-- 多货币/站点 -->
        <template v-if="isMultiSiteShow && pageMultiSiteSwitch.includes(2)">
          <login-multi-currency />
        </template>
        <!-- 手机号 -->
        <v-text-field
          v-if="isTel"
          v-model="params.tel"
          :label="$t('UI.Login_0_main_1')"
          background-color="t_login_input_bg"
          color="rgba(255, 255, 255, 0.4)"
          type="number"
          clearable
          hide-details
          rounded
          outlined
          filled
          class="form_field form_text_field form_phone"
        >
          <template slot="prepend-inner">
            <user-select
              :value="params.area"
              :userIcon="$u.optionalChaining(phonePlaces[0], 'flag')"
              :list="phonePlaces"
              @change="par => (params.area = par.code)"
            />
          </template>
        </v-text-field>
        <!-- 邮箱 -->
        <inputAutocompleteSuffix
          v-else
          v-model="params.email"
          :label="$t('UI_EnterMail')"
          background-color="t_login_input_bg"
          class="email_wrap form_field form_text_field"
          color="rgba(255, 255, 255, 0.4)"
          clearable
          rounded
          outlined
          filled
          :radius="variables.login_regist_field_radius"
          :suffix-list="emailSuffix"
          :title="$t('UI_Login_Email_Suffix')"
        />
        <!-- :rules="emailRules"  占时先不要  -->
        <!-- 密码 -->
        <v-text-field
          v-model="params.password"
          :label="$t('UI.Login_0_main_2')"
          :type="!pwdState ? 'password' : 'text'"
          background-color="t_login_input_bg"
          color="rgba(255, 255, 255, 0.4)"
          clearable
          hide-details
          rounded
          outlined
          filled
          class="form_field form_text_field"
        >
          <template slot="append">
            <div class="cursor_point" @click="pwdState = !pwdState">
              <icon-font
                :name="pwdState ? 'icon-Icon_Eye' : 'icon-Icon_Closseeye'"
                :size="variables.login_regist_pwd_icon_size"
              />
            </div>
          </template>
        </v-text-field>
        <!-- 登录 -- 条款，找回密码 -->
        <div v-if="isLogin" class="checkbox_show remember_wrap">
          <check-box v-model="isRemeber" class="">
            <div class="check_text">{{ $t('UI_LOGIN_remember') }}</div>
          </check-box>
          <div class="warning_text cursor_point" @click="onForgotPwd">
            {{ $t('UI_LOGIN_forgetPwd') }}
          </div>
        </div>
        <!-- 注册，验证码，条款 -->
        <template v-else>
          <!-- 电话验证码-->
          <v-text-field
            v-if="isTel && systemConfig.user_mobile_reg_switch"
            v-model="params.code"
            :label="$t('UI.Login_0_main_10')"
            background-color="t_login_input_bg"
            color="rgba(255, 255, 255, 0.4)"
            type="number"
            clearable
            filled
            hide-details
            rounded
            outlined
            class="form_field form_text_field"
          >
            <template slot="append">
              <v-btn
                :height="variables.login_regist_code_btn_height"
                :width="variables.login_regist_code_btn_width"
                color="t_primary_dark_color"
                :disabled="!params.tel"
                class="btn_radius"
                @click="getCode"
              >
                <div>{{ countDown }}</div>
              </v-btn>
            </template>
          </v-text-field>
          <!-- 邮箱验证码 -->
          <v-text-field
            v-if="!isTel && systemConfig.user_email_reg_switch"
            v-model="params.code"
            :label="$t('UI.Login_0_main_10')"
            background-color="t_login_input_bg"
            color="rgba(255, 255, 255, 0.4)"
            type="number"
            clearable
            filled
            hide-details
            rounded
            outlined
            class="form_field form_text_field"
          >
            <template slot="append">
              <v-btn
                :height="variables.login_regist_code_btn_height"
                :width="variables.login_regist_code_btn_width"
                color="t_primary_dark_color"
                :disabled="!params.email"
                class="btn_radius"
                @click="getEmailCode"
              >
                <div>{{ countEmailDown }}</div>
              </v-btn>
            </template>
          </v-text-field>
          <!-- 出生年月 -->
          <div v-if="systemConfig.user_age_limit_switch" class="birthday_limit">
            <div class="age_title">{{ $t('UI_Login_CPF_Birth') }}</div>
            <div class="age_birthday" :class="{ fail: birthdayError }">
              <birthday-age v-model="params.birthday" />
            </div>
          </div>
          <!-- 条款 -->
          <div class="register_clause receive_wrap">
            <check-box v-model="receiveClause">
              <div class="check_text">
                {{ $t('UI_LOGIN_clause') }}
                <span class="register_title" @click.stop="serviceLinkClick">
                  &nbsp;{{ $t('UI_Terms_of_Service') }}
                </span>
              </div>
            </check-box>
            <check-box v-model="receiveMarketing">
              <div class="check_text">{{ isTel ? $t('UI_LOGIN_tel_marketing') : $t('UI_LOGIN_email_marketing') }}</div>
            </check-box>
          </div>
        </template>
      </v-form>
      <v-btn
        class="login_btn btn_radius"
        color="t_primary_dark_color"
        width="100%"
        :height="variables.login_regist_btn_height"
        @click="onSubmit"
      >
        <div class="login_btn_text">{{ isLogin ? $t('UI_Login') : $t('UI_Create_account') }}</div>
      </v-btn>

      <template v-if="recaptchaState">
        <vue-recaptcha-verify
          ref="recaptchaRef"
          v-model="recaptchaState"
          :titleText="$t('UI.Login_Regist_Tips')"
          :contentText="$t('UI.Login_Regist_Content_Text')"
          @onSuccess="onSuccess"
          @onBack="onBack"
        />
      </template>
      <tips-box
        :isLogin="isLogin"
        :cpfRegistData="cpf_regist_data"
        @loginRegisterSuccess="loginRegisterSuccess"
        @close="closeParent"
      />
      <!-- 忘记密码弹窗 -->
      <forgot-pwd ref="forgotPwdRef" />
    </template>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { loadWebsocket } from '@/server/ws.js'
import loadIcon from '@/assets/icons'
import UserSelect from '@/components/ui/user-select'
import TipsBox from '@/components/login/tips-box'
import forgotPwd from './forgot-password.vue'
import CheckBox from '@/components/ui/CheckBox/index'
import VueRecaptchaVerify from '@/components/ui/vue-recaptcha-verify/index'
import inputAutocompleteSuffix from '@/components/ui/input-autocomplete-suffix'
import variables from '@style/components/game-container.login.modules.scss'
import CpfRegist from './cpf-regist'
import birthdayAge from '@/components/ui/birthday-age/index'
import LoginMultiCurrency from '@/components/ui/login-multi-currency'
import PublishSubscribe from 'publish-subscribe-js'
import MenuType from '@/events/menu.js'
import { Base64 } from 'js-base64'
import { getFormatDate, getTimeStamp } from '@/utils/CommonJs'
import { sessionStorage } from '@/utils/StorageUtils'
import { login, register, get_code, getEmailcode } from '@/api/modules/login'
import { lodash, isEmail, AdjustlogEvent } from '@/utils/helper'
import { AJ_DOTTING } from '@/utils/dotting.js'
import { hasShowGuideInstall } from '@/utils/hasShowGuideInstall'
import { ADS_TYPE, EVENT_KEY, getFacebookPixelId, dynamicInitAds, logEvent } from '@/server/ads/index'
import { getEnvConfig } from '@/utils/collect.js'

const _date = new Date()
// 18年前的今天
const date_18 = getFormatDate({ date: new Date(_date.getFullYear() - 18, _date.getMonth(), _date.getDate()) })

export default {
  components: {
    UserSelect,
    forgotPwd,
    TipsBox,
    CheckBox,
    VueRecaptchaVerify,
    inputAutocompleteSuffix,
    CpfRegist,
    birthdayAge,
    LoginMultiCurrency
  },
  props: {
    // 是否是登录 一级Tab选项
    isLogin: {
      type: Boolean,
      default: false
    },
    // 是否是手机号输入 二级Tab选项
    isTel: {
      type: Boolean,
      default: false
    },
    isCpfVerify: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      variables,
      showHeight: 48, // input 框高度 || 底部按钮高度
      pwdState: false, // 密码是否显示
      countDown: this.$t('UI.Login_0_main_10'), // 倒计时
      countEmailDown: this.$t('UI.Login_0_main_10'), // 倒计时
      params: {
        // 参数
        tel: '', // 手机号
        area: '', // 手机号区号
        email: '', // 邮件
        password: '', // 密码
        code: '',
        birthday: date_18 // 出生年月
      },
      isRemeber: true, // 记住账号密码 多选框
      receiveClause: true, // 条款
      receiveMarketing: true, // 发送邮件
      codeValue: 0,
      codeEmailValue: 0,
      timerInterval: null,
      serviceLink: '',
      recaptchaState: false, // 谷歌图片开关
      recaptcha: '', // 谷歌图片验证返回值
      currentRecaptcha: 1, // 1 getcode验证码 2 登录验证
      emailSuffix: [
        '@gmail.com',
        '@hotmail.com',
        '@outlook.com',
        '@live.com',
        '@icloud.com',
        '@yahoo.com',
        '@protonmail.com',
        '@gmx.com',
        '@mail.com',
        '@sinos.net'
      ], // 邮箱尾缀列表
      verify_img_params: '', // 图形自验验证码加密code
      emailRules: [v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || this.$t('UI_Complete_Information')],
      valid: false, // 表单校验结果标记
      cpf_regist_data: {},
      birthdayError: false, // 出生日期错误
      pixelid: ''
    }
  },
  computed: {
    ...mapState('source', ['phonePlaces']),
    ...mapState('base', [
      'isApp',
      'user',
      'languageType',
      'showRedDot',
      'systemConfig',
      'adJustKey',
      'platform',
      'deviceid'
    ]),
    ...mapGetters('base', ['getRootParams']),
    ...mapGetters('common', ['isMultiSiteShow', 'pageMultiSiteSwitch']),
    isIosStandalone() {
      return 'standalone' in window.navigator && window.navigator['standalone']
    },
    // 邮箱验证
    verificationEmail() {
      return [v => !!v || this.$t('UI_LOGIN_emailEmpty'), v => (v && isEmail(v)) || this.$t('UI_LOGIN_emailFormat')]
    }
  },
  watch: {
    isLogin: {
      immediate: true,
      handler(val) {
        // this.$refs.form.reset()
        this.getSession()
      }
    },
    isTel: {
      immediate: true,
      handler() {
        this.isLogin && this.getSession()
      }
    }
  },
  mounted() {
    const { privacy = '' } = this.systemConfig
    this.serviceLink = privacy
    this.isLogin && this.getSession()
    this.pixelid = getFacebookPixelId()
  },
  beforeDestroy() {
    clearInterval(this.timerInterval)
    this.timerInterval = null
  },
  methods: {
    onCaptchaVerify(response) {
      this.isCaptchaVerified = true
    },
    onRecaptchaVerify(token) {
      // 处理reCAPTCHA验证成功后的逻辑
    },
    loadIcon: type => loadIcon(type, false),
    clearUserInfo() {
      this.params.tel = ''
      this.params.area = this.phonePlaces[0]?.code
      this.params.email = ''
      this.params.password = ''
      this.params.code = ''
      this.params.birthday = date_18
    },
    // 获取缓存里的账号密码
    getSession() {
      // 检查是否有本地存储的登录信息
      if (localStorage.getItem('webLoginCode') && this.isLogin) {
        let { mail_account, mail_password, phone_account, phone_password, code } = JSON.parse(
          localStorage.getItem('webLoginCode')
        )
        if (this.isTel) {
          this.params.tel = phone_account
          // 如果有手机号码
          if (this.params.tel) {
            this.params.area = code || this.phonePlaces[0]?.code
            this.params.password = Base64.decode(phone_password)
          } else {
            this.params.area = this.phonePlaces[0]?.code
            this.params.password = ''
          }
        } else {
          this.params.email = mail_account
          if (this.params.email) {
            this.params.password = Base64.decode(mail_password)
          } else {
            this.params.password = ''
          }
        }
      } else {
        this.clearUserInfo()
      }
    },
    // 注册，获取验证码
    async getCode() {
      if (!this.params.tel) return
      if (this.codeValue > 0) return
      const { language } = this.getRootParams
      const { tel = '', area = '' } = this.params
      let areaNew = area.replace('+', '')
      areaNew = lodash.isBlank(areaNew) ? this.phonePlaces[0]?.code.replace('+', '') : areaNew
      let par = { area: areaNew, tel: areaNew + '' + tel, type: 0, language, device_id: this.deviceid }

      if (this.recaptchaState) {
        par.recaptcha = this.recaptcha
      }

      // 图形自验验证码加密串
      if (this.verify_img_params) par.verify_img_params = this.verify_img_params
      this.verify_img_params = ''

      const { code, msg, data } = await get_code(par).catch(e => e)
      if (code == 0) {
        this.codeValue = data || 60
        this.countDown = this.codeValue + 's'
        this.timerInterval = setInterval(() => {
          if (this.codeValue > 0) {
            this.countDown = this.codeValue + 's'
            this.codeValue--
          } else {
            this.countDown = this.$t('UI.Login_0_main_10')
            clearInterval(this.timerInterval)
            this.timerInterval = null
          }
        }, 1000)
      }
      // 谷歌验证码弹起来
      if (code == 300) {
        this.currentRecaptcha = 1
        this.recaptchaState = true
        return
      }
      // code == 301 图形验证自验验证码
      if (code == 301) {
        const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = data || {}
        const resDrag = await this.$pop
          .imgDrag({ baseSrc, checkCode, checkSrc })
          .show()
          .catch(e => e)
        if (resDrag == 'cancel') return

        const { encrypted = '' } = resDrag
        this.verify_img_params = encrypted
        this.getCode()
        return
      }
      // 关闭谷歌图片验证弹窗
      this.recaptchaState = false
      this.$pop.message().show({ content: msg })
      // if (this.$refs.vCode.canGetCode) this.$refs.vCode.start();
    },
    // 邮箱验证码
    async getEmailCode() {
      const { email = '' } = this.params
      // 验证邮箱
      if (!email) return
      if (!isEmail(email)) return
      if (this.codeEmailValue > 0) return
      const paramsEmail = {
        email,
        language: this.languageType,
        type: 0,
        device_id: this.deviceid
      }
      // if (this.recaptchaState) {
      // 	parEmail.recaptcha = this.recaptcha;
      // }

      // 图形自验验证码加密串
      // if (this.verify_img_params) parEmail.verify_img_params = this.verify_img_params;
      // this.verify_img_params = '';
      // this.$pop.loading().show()
      const { code, data, msg } = await getEmailcode(paramsEmail).catch(e => e)
      // this.$pop.loading().hide()
      this.$pop.message().show({ content: msg })
      if (code != 0) return
      this.codeEmailValue = data || 60
      this.countEmailDown = this.codeEmailValue + 's'
      this.timerInterval = setInterval(() => {
        if (this.codeEmailValue > 0) {
          this.countEmailDown = this.codeEmailValue + 's'
          this.codeEmailValue--
        } else {
          this.countEmailDown = this.$t('UI.Login_0_main_10')
          clearInterval(this.timerInterval)
          this.timerInterval = null
        }
      }, 1000)
      // 谷歌验证码弹起来
      // if (res && res.code == 300) {
      // 	this.currentRecaptcha = 3;
      // 	this.recaptchaState = true;
      // 	return;
      // }
      // code == 301 图形验证自验验证码
      // if (res && res.code == 301) {
      // 	const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = res.data || {};
      // 	const resDrag = await this.$pop
      // 		.imgDrag({ baseSrc, checkCode, checkSrc })
      // 		.show()
      // 		.catch((e) => e);
      // 	if (resDrag == 'cancel') return;

      // 	const { encrypted = '' } = resDrag;
      // 	this.verify_img_params = encrypted;
      // 	this.onConfirmEmail();
      // 	return;
      // }

      // 关闭谷歌图片验证弹窗
      // this.recaptchaState = false;
    },
    // 登录打点
    handleLoginLog() {
      logEvent(EVENT_KEY.login)
    },
    // 注册打点
    handleRegistrationLog() {
      AdjustlogEvent(this.systemConfig, 'register')
      logEvent(EVENT_KEY.registration)
      setTimeout(() => {
        this.handleLoginLog()
      }, 500)
    },
    // 提交
    async onSubmit() {
      if (!this.isLogin && hasShowGuideInstall(1)) {
        this.$pop.guideInstall().show()
        return
      }

      // 注册的时候没有勾选【条款，发送邮件】，不能注册
      if (!this.isLogin && (!this.receiveClause || !this.receiveMarketing))
        return this.$pop.message().show({ content: this.$t('UI_Register_Clause') })

      let { tel = '', area = '', email = '', code: get_code = '', password = '', birthday } = this.params

      // 处理空格
      tel = String(tel).replace(/\s+/g, '')
      area = String(area).replace(/\s+/g, '')
      email = String(email).replace(/\s+/g, '')
      get_code = String(get_code).replace(/\s+/g, '')
      password = String(password).replace(/\s+/g, '')

      // 注册时密码格式校验
      if (!this.isLogin && !this.$u.isPwdCheck(password))
        return this.$pop.message().show({ content: this.$t('UI.Pwd_Format_Check_Text') })
      // 注册未成年校验
      if (!this.isLogin) {
        // 电话开启了防未成年 或者 邮箱开启了防未成年 && 未满18岁
        if (this.systemConfig.user_age_limit_switch && !getTimeStamp(date_18, birthday)) {
          return this.$pop.message().show({ content: this.$t('UI_Birth_Invalid') })
        }
      }

      // AJ 打点上报（游客）
      AJ_DOTTING.getAjReporting()

      area = area.replace('+', '')
      area = lodash.isBlank(area) ? this.phonePlaces[0]?.code.replace('+', '') : area
      // 邀新有礼，link_source识别字段获取sessionStorage
      const link_source = this.$route.query.link_source || sessionStorage.get('link_source') || ''
      let par = {
        [!this.isLogin && this.isTel ? 'tel' : 'account']: this.isTel ? area + tel : email,
        [this.isLogin ? 'password' : 'pwd']: password,
        login_type: this.isTel ? 1 : 2,
        ...this.getRootParams,
        pixelid: this.pixelid,
        domain: getEnvConfig.baseURL(), // 来源域名
        device_id: this.deviceid,
        link_source
      }

      // 开启了防止未成年限制
      if (this.systemConfig.user_age_limit_switch) {
        par.birthday = +new Date(birthday) / 1000
      }

      // 如果开启cpf
      if (this.systemConfig.reg_cpf_control_switch == 1 && !this.isLogin) {
        par = { ...par, ...this.cpf_regist_data }
      }
      if (this.recaptchaState) {
        par.recaptcha = this.recaptcha
      }
      // 图形自验验证码加密串
      if (this.verify_img_params) par.verify_img_params = this.verify_img_params
      this.verify_img_params = ''

      // 判断是否是壳包，并且有adJustKey归因使用adJustKey为agentid
      if (this.adJustKey) {
        if (!this.isLogin) {
          const { trackerName, trackerToken } = this.adJustKey
          par.agentid = trackerToken
          par.ajtrackerName = trackerName
        }
      } else {
        let agentid = localStorage.getItem('agentid')
        if (!agentid || agentid == 'null') agentid = ''
        if (!this.isLogin) {
          par.agentid = agentid
        }
      }
      let source_type = localStorage.getItem('source_type')
      if (!source_type) source_type = ''
      par.source_type = source_type

      if (this.platform == 'ios') {
        // 判断是否ios桌面
        if (this.isIosStandalone) {
          par.ioswebclip = 1
        } else {
          par.ioswebclip = 0
        }
      }
      if (this.isTel) {
        par.area = area
      }
      par.code = get_code
      this.$pop.loading().show()
      const getInterface = this.isLogin ? login : register
      let { code, data, msg } = await getInterface(par).catch(e => e)
      this.$pop.loading().hide()
      // code == 300 弹谷歌验证图
      if (code == 300) {
        this.currentRecaptcha = 2
        this.recaptchaState = true
        return
      }
      // code == 301 图形验证自验验证码
      if (code == 301) {
        const { code: checkCode = '', img: baseSrc = '', url: checkSrc = '' } = data || {}
        const resDrag = await this.$pop
          .imgDrag({ baseSrc, checkCode, checkSrc })
          .show()
          .catch(e => e)
        if (resDrag == 'cancel') return

        const { encrypted = '' } = resDrag
        this.verify_img_params = encrypted
        this.onSubmit()
        return
      }

      // 关闭谷歌图片验证弹窗
      this.recaptchaState = false
      if (code != 0) return this.$pop.message().show({ content: msg })
      // 每日可玩登录限制
      const dayPlayTime = data.day_play_time || {}
      const day_play_time = dayPlayTime.day_play_time || {}
      if (dayPlayTime.is_day_play_time != 1) {
        // is_day_play_time: 1 可玩，0 不可玩
        const params = {
          currentToken: data.token,
          startH: day_play_time.start_hour,
          startM: day_play_time.start_min,
          endH: day_play_time.end_hour,
          endM: day_play_time.end_min,
          editTime: dayPlayTime.edit_cd_time,
          error: () => {
            this.onSubmit()
          }
        }
        this.$pop.disableLogin(params).show()
        return
      }
      // 防沉迷限制登录
      const { responsible_login_limit = {}, token = '' } = data
      if (responsible_login_limit && Object.keys(responsible_login_limit).length > 0) {
        // limit_start_time 开始时间 limit_end_time 结束时间 edit_cd_time 可设置时间
        const { limit_start_time = 0, limit_end_time = 0, edit_cd_time = 0 } = responsible_login_limit
        this.$pop
          .limitLogin({
            currentToken: token,
            startTime: limit_start_time,
            endTime: limit_end_time,
            editTime: edit_cd_time,
            error: () => {
              this.onSubmit()
            }
          })
          .show()

        return
      }
      // 如果勾选isRemebe存储账号密码，否则存储密码清空
      localStorage.setItem(
        'webLoginCode',
        JSON.stringify({
          code: data.code || this.phonePlaces[0]?.code,
          mail_account: data.account,
          mail_password: this.isRemeber ? Base64.encode(password) : '',
          phone_account: data.tel,
          phone_password: this.isRemeber ? Base64.encode(password) : ''
        })
      )
      this.$emit('close')
      this.$emit('loginSuccess')
      this.loginRegisterSuccess({ data })
    },
    // 登录/注册成功后操作
    async loginRegisterSuccess({ data = {} }) {
      const { token = '', apk_info = { apk_send_gold: 0, apk_show_switch: 0 }, pixelid } = data

      this.$store.commit('base/SET_SHOW_RED_DOT', {})
      this.$store.commit('base/SET_USER', data)
      this.$store.commit('base/SET_USER_HISTORY', data)
      loadWebsocket(this)

      // 获取下载金币信息 / 壳包下载 活动充值获取金币
      this.$store.commit('base/SET_APK_ACTIVITY_GOLD', apk_info)
      // aj 打点
      this.$store.dispatch('base/getAjNoticeFun')
      // 打点
      const logHandler = this.isLogin ? this.handleLoginLog : this.handleRegistrationLog
      logHandler()
      // 同步本地 facebooke ads 缓存
      if (this.isLogin && pixelid) dynamicInitAds(ADS_TYPE.facebook)
      // 请求防沉迷配置接口
      this.$store.dispatch('responsible/getFeatureInfo', {
        token,
        language: this.languageType,
        callback: () => {
          // 防沉迷更新提醒功能倒计时本地缓存数据
          this.$store.dispatch('responsible/renewRemindStorage', { sign: 'login', data })
        }
      })
      // 请求红点提示
      await this.$store
        .dispatch('base/showRedDotReceivedFun', { version: 1, token, language: this.languageType })
        .catch(e => e)
      // 邀请活动登录需要获取新的信息
      await this.$store.dispatch('source/getActivityConfigFn', { token, language: this.languageType }).catch(e => e)
      // 获取回归任务弹窗数据
      await this.$store.dispatch('base/getRegressionDialog', { token, language: this.languageType }).catch(e => e)
      // 获取vip签到列表
      await this.$store.dispatch('common/getVipSignList')

      // 通知站内信，更新信息
      PublishSubscribe.publish(MenuType.UPDATE_EMAIL)
      // 清除弹窗，根据页面重新初始化
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_CLEAR)
      this.$nextTick(() => {
        PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, this.$route.name)
      })
    },
    // 忘记密码
    onForgotPwd() {
      this.$nextTick(async () => {
        const params = {
          sign: this.isTel ? 'tel' : 'email',
          par: this.params
        }
        const res = await this.$refs['forgotPwdRef']?.open(params).catch(e => e)
        if (res == 'ok') return this.$store.dispatch('modal/setLoginRegistShow', { show: false })
      })
    },
    closeParent() {
      this.handleLoginLog()
      this.$emit('close')
    },
    serviceLinkClick() {
      // 兼容 移动端safari浏览器无法通过window.open在新标签打开链接
      setTimeout(() => window.open(this.serviceLink, '_blank'))
    },
    // 谷歌验证码成功后返回数据
    onSuccess(res) {
      this.recaptcha = res
      if (this.currentRecaptcha == 1) {
        this.getCode()
      }
      if (this.currentRecaptcha == 2) {
        this.onSubmit()
      }
    },
    onBack() {
      this.recaptchaState = false
    },
    onCpfSubmit(e) {
      if (e.birthday) {
        e.birthday = new Date(e.birthday).getTime()
      }
      this.cpf_regist_data = e
      this.$emit('OnClickCpfVerify', true)
    }
  }
}
</script>

