<template>
  <!-- 参考tip-box 组件 提供通用API调用方式  -->
  <div v-if="isShowLogin.google || isShowLogin.facebook || isShowLogin.telegram">
    <!-- 第三方登录 -->
    <!-- style="display: none;" 为了不动旧的逻辑，现在加style="display: none;"更加ID触发谷歌登录和fb登录 （单子：6737）-->
    <div v-zinx class="tripartite_dialog" v-if="thirdDialogState" style="display: none">
      <div class="wrapper-mask" @click="onClose"></div>
      <div class="tripartite_box">
        <div ref="tripartitePopRef" class="tripartite_pop dialog_radius">
          <div class="tripartite_box_title w_100 text_center">
            {{ $t('General.General_9') }}
            <div class="close_icon cursor_point d-flex align-center" @click="onClose">
              <icon-font
                name="icon-Icon_Cloose"
                :size="variables.tip_box_tripartite_dialog_close_icon_size"
              ></icon-font>
            </div>
          </div>
          <div class="w_100 tripartite_box_body">
            <div class="text_center tripartite_box_body_text">
              {{ tripartiteText }}
            </div>
            <div class="tripartite_box_body_btn">
              <g-signin-button
                :params="googleSignInParams"
                @success="onGSignInSuccess"
                @error="onGSignInError"
                v-if="loginType == 1"
              >
                <v-btn
                  width="100%"
                  color="t_primary_dark_color"
                  id="googleId"
                  :height="variables.tip_box_tripartite_dialog_body_btn_height"
                  class="btn_radius"
                >
                  <icon-font
                    name="icon-Icon_Google"
                    :size="variables.tip_box_tripartite_dialog_body_btn_icon_size"
                  ></icon-font>
                  <span class="tripartite_box_body_btn_text">{{ $t('UI_Google_btn') }}</span>
                </v-btn>
              </g-signin-button>
              <!-- 非原生 -->
              <fb-signin-button
                :params="fbSignInParams"
                @success="onFBSignInSuccess"
                @error="onFBSignInError"
                v-if="!android && loginType == 2"
              >
                <v-btn
                  width="100%"
                  color="t_primary_dark_color"
                  id="fbId"
                  :height="variables.tip_box_tripartite_dialog_body_btn_height"
                  class="btn_radius"
                >
                  <icon-font
                    name="icon-Icon_FaceBook2"
                    :size="variables.tip_box_tripartite_dialog_body_btn_icon_size"
                  ></icon-font>
                  <span class="tripartite_box_body_btn_text">{{ $t('UI_Facebook_btn') }}</span>
                </v-btn>
              </fb-signin-button>
              <!-- 原生 -->
              <v-btn
                v-if="android && loginType == 2"
                width="100%"
                color="t_primary_dark_color"
                :height="variables.tip_box_tripartite_dialog_body_btn_height"
                class="btn_radius"
                @click="loginFaceBook"
              >
                <icon-font
                  name="icon-Icon_FaceBook2"
                  :size="variables.tip_box_tripartite_dialog_body_btn_icon_size"
                ></icon-font>
                <span class="tripartite_box_body_btn_text">{{ $t('UI_Facebook_btn') }}</span>
              </v-btn>
              <v-btn
                v-if="loginType == 4"
                width="100%"
                color="t_primary_dark_color"
                id="telegramId"
                :height="variables.tip_box_tripartite_dialog_body_btn_height"
                class="btn_radius"
                @click="loginTelegram"
              >
                <icon-font
                  name="icon-icon_telegreat"
                  :size="variables.tip_box_tripartite_dialog_body_btn_icon_size"
                ></icon-font>
                <span class="tripartite_box_body_btn_text">{{ $t('UI_Telegram_btn') }}</span>
              </v-btn>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import loadIcon from '@/assets/icons'
import { GOOGLE_ID, FACEBOOK_ID, TELEGRAM_BOT_ID, POP_TIME } from '@/api/setting'
import { thirdLogin } from '@/api/modules/login'
import { lodash } from '@/utils/helper.js'
import { gapi } from 'gapi-script' //谷歌登陆需要 勿删除
import { getEnvConfig } from '@/utils/collect.js'
import variables from '@style/components/game-container.login.modules.scss'
import { AJ_DOTTING } from '@/utils/dotting.js'
import { hasShowGuideInstall } from '@/utils/hasShowGuideInstall'

export default {
  name: 'ThirdLogin',
  props: {
    isLogin: Boolean
  },
  data() {
    return {
      variables,
      iconSize: 36,
      thirdDialogState: true, // 登录弹窗
      loginType: 1, // 1 fackbook 2 google 4 telegram
      faceScope: {},
      faceFB: {},
      faceModel: {},
      serviceLink: '',
      ///-----------外部登入
      fbSignInParams: {
        scope: 'email,user_likes',
        return_scopes: true
      },
      googleSignInParams: {
        client_id: GOOGLE_ID
      },
      android: window.android
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'systemConfig', 'languageType']),
    ...mapGetters('base', ['getShowConfig']),
    tripartiteText() {
      const KeyMap = {
        1: 'UI_Google_text',
        2: 'UI_Facebook_text',
        4: 'UI_Telegram_text'
      }

      const key = KeyMap[this.loginType]
      return this.$t(key)
    },
    // 控制登录选项
    isShowLogin() {
      const google = this.getShowConfig('google_login') == 1
      const facebook = this.getShowConfig('facebook_btn') == 1
      const telegram = this.getShowConfig('telegram_login') == 1

      return {
        google,
        facebook,
        telegram
      }
    }
  },
  mounted() {
    const { privacy = '' } = this.systemConfig
    this.serviceLink = privacy
    if (this.android) {
      window.android.callScript = this.callScript
    } else {
      window.fbAsyncInit = function () {
        FB.init({
          appId: FACEBOOK_ID,
          cookie: true,
          xfbml: true,
          version: 'v2.8'
        })

        FB.AppEvents.logPageView()
      }
    }
    this.initSDKScript()
  },
  methods: {
    initSDKScript() {
      const insertScript = function (link, id) {
        var js,
          fjs = document.getElementsByTagName('script')[0]
        if (document.getElementById(id)) {
          return
        }
        js = document.createElement('script')
        js.id = id
        js.src = link
        fjs.parentNode.insertBefore(js, fjs)
      }
      // 如果不是安卓壳包，fb需要加载sdk
      if (!this.android) insertScript('https://connect.facebook.net/en_US/sdk.js', 'facebook-jssdk')

      insertScript('https://telegram.org/js/telegram-widget.js', 'telegram-widget')
    },

    /**
     * @Author: chenc
     * @Date: 2023-03-07 10:24:21
     * @description: 原生android回调
     * @param {String} methodname 方法名称
     * @param {String} callVal 回调值
     * @return {String}
     */
    callScript(methodname, callVal) {
      if (methodname === 'loginFaceBookSuccess') {
        const json = JSON.parse(callVal)
        let form = {
          nickname: json.name,
          third_token: json.uid,
          appid: json.appid || '',
          access_token: json.accessToken || ''
        }
        this.submitLogin(form)
      }
    },

    signIn(signInType) {
      switch (signInType) {
        case 'google':
          this.showGoogle()
          break
        case 'facebook':
          this.showFacebook()
          break
        case 'telegram':
          this.showTelegram()
          break
        default:
          break
      }
    },

    // 原生android跳转facebook
    loginFaceBook() {
      this.$u.callJava('loginFaceBook')
    },

    ///-----------外部登入:方法
    // 谷歌登录按钮点击
    showGoogle() {
      if (hasShowGuideInstall(1) && !this.isLogin) {
        this.$pop.guideInstall().show()
        return
      }
      this.thirdDialogState = true
      this.$u.callJava('openInternalWindow', { value: true })
      this.loginType = 1
      this.$nextTick(() => {
        const tripartitePopRef = this.$refs['tripartitePopRef']
        if (tripartitePopRef) this.$anim.insert(tripartitePopRef)
      })
      // 触发google登录按钮事件
      this.$nextTick(() => {
        let button = document.getElementById('googleId')
        button.click()
      })
    },
    // 谷歌登录回调成功
    onGSignInSuccess(googleUser) {
      // `googleUser` is the GoogleUser object that represents the just-signed-in user.
      // See https://developers.google.com/identity/sign-in/web/reference#users
      let form = {
        nickname: googleUser.getBasicProfile().getName(),
        third_token: googleUser.getBasicProfile().getId(),
        headimg: googleUser.getBasicProfile().getImageUrl(),
        third_account: googleUser.getBasicProfile().getEmail(),
        third_jwt: googleUser.getAuthResponse().id_token,
        login_type: 1
      }
      this.submitLogin(form)
    },
    // 谷歌登录回调失败
    onGSignInError(error) {
      this.thirdDialogState = false
      this.$pop.message().show({ content: this.$t('UI_externo_login') })
    },

    // Facebook 登录按钮点击
    showFacebook() {
      if (hasShowGuideInstall(1) && !this.isLogin) {
        this.$pop.guideInstall().show()
        return
      }
      this.thirdDialogState = true
      this.loginType = 2
      this.$nextTick(() => {
        const tripartitePopRef = this.$refs['tripartitePopRef']
        if (tripartitePopRef) this.$anim.insert(tripartitePopRef)
      })
      if (this.android) {
        // 触发原生Facebook登录按钮事件
        this.loginFaceBook()
      } else {
        // 触发非原生Facebook登录按钮事件
        this.$nextTick(() => {
          let button = document.getElementById('fbId')
          button.click()
        })
      }
    },
    // Facebook登录回调成功
    onFBSignInSuccess(response) {
      FB.api('/me', params => {
        let form = {
          nickname: params.name,
          third_token: params.id,
          appid: params.appid || '',
          access_token: params.accessToken || ''
        }
        this.submitLogin(form)
      })
    },
    // Facebook登录失败
    onFBSignInError(msg) {
      this.thirdDialogState = false
      this.$pop.message().show({ content: this.$t('UI_externo_login') })
    },
    // Telegram登录
    showTelegram() {
      if (hasShowGuideInstall(1) && !this.isLogin) {
        this.$pop.guideInstall().show()
        return
      }
      if (this.android) {
        this.$u.callJava('openInternalWindow', { value: true })
      }
      this.thirdDialogState = true
      this.loginType = 4
      this.$nextTick(() => {
        const tripartitePopRef = this.$refs['tripartitePopRef']
        if (tripartitePopRef) this.$anim.insert(tripartitePopRef)
      })
      this.$nextTick(() => {
        // 触发Telegram登录按钮事件
        this.loginTelegram()
      })
    },

    loginTelegram() {
      window.Telegram.Login.auth({ bot_id: TELEGRAM_BOT_ID, request_access: 'write', embed: 1 }, data => {
        if (!data) return
        console.log('Telegram.Login.auth: ', data)
        const { first_name = '', last_name = '', photo_url, id, hash, auth_date } = data
        const form = {
          nickname: first_name + last_name,
          headimg: photo_url,
          third_account: id,
          third_jwt: hash,
          auth_date,
          first_name,
          last_name,
          // third_token: hash,
          login_type: 4
        }
        this.submitLogin(form)
      })
    },
    //------------------------
    loadIcon: type => loadIcon(type, false),
    // 第三方登录 接口
    submitLogin(params) {
      // AJ 打点上报（游客）
      AJ_DOTTING.getAjReporting()

      let roots = this.$store.getters['base/getRootParams']
      let source_type = localStorage.getItem('source_type')
      const agentid = localStorage.getItem('agentid') || ''
      if (!source_type) source_type = ''
      let form = {
        deviceid: roots.deviceid,
        platform: roots.platform,
        mainVer: roots.mainVer,
        subVer: roots.subVer,
        pkgName: roots.pkgName,
        language: roots.language,
        agentid: agentid,
        source_type: source_type,
        third_token: params.third_token, //access_token, id_token待测试
        nickname: params.nickname,
        headimg: params.headimg || '',
        third_account: params.third_account || '',
        third_jwt: params.third_jwt || '',
        login_type: params.login_type || '',
        auth_date: params.auth_date || '',
        first_name: params.first_name || '',
        last_name: params.last_name || '',
        appid: params.appid || '',
        access_token: params.access_token || '',
        domain: getEnvConfig.baseURL() // 来源域名
      }
      thirdLogin({ url: getEnvConfig.baseURL(), form }).then(({ code, data, msg }) => {
        if (code != 0) return this.$pop.message().show({ content: msg })

        const { token = '', responsible_login_limit = {} } = data

        // 每日可玩登录限制
        const dayPlayTime = data.day_play_time || {}
        const day_play_time = dayPlayTime.day_play_time || {}
        if (dayPlayTime.is_day_play_time != 1) {
          // is_day_play_time: 1 可玩，0 不可玩
          const params = {
            currentToken: token,
            startH: day_play_time.start_hour,
            startM: day_play_time.start_min,
            endH: day_play_time.end_hour,
            endM: day_play_time.end_min,
            editTime: dayPlayTime?.edit_cd_time || 0,
            error: () => {
              this.submitLogin(params)
            }
          }
          this.$pop.disableLogin(params).show()
          return
        }
        // 防沉迷限制登录
        if (responsible_login_limit && Object.keys(responsible_login_limit).length > 0) {
          // limit_start_time 开始时间 limit_end_time 结束时间 edit_cd_time 可设置时间
          const { limit_start_time = 0, limit_end_time = 0, edit_cd_time = 0 } = responsible_login_limit
          this.$pop
            .limitLogin({
              currentToken: token,
              startTime: limit_start_time,
              endTime: limit_end_time,
              editTime: edit_cd_time,
              error: () => {
                this.submitLogin(params)
              }
            })
            .show()

          return
        }
        this.thirdDialogState = false
        this.$emit('loginRegisterSuccess', { data, isLogin: true })
        this.$emit('close')
      })
    },
    // 关闭第三方弹窗
    onClose() {
      this.$nextTick(() => {
        const tripartitePopRef = this.$refs['tripartitePopRef']
        if (tripartitePopRef) this.$anim.close(tripartitePopRef)
      })

      setTimeout(() => {
        this.thirdDialogState = false
        this.$u.callJava('openInternalWindow', { value: false })
      }, POP_TIME)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.login.modules.scss';

// 针对移动端样式
.comp.tip_box {
  @include setBox(column, flex-start, center);
  .text_single {
    width: 100%;
    position: relatvie;
    @include text_ellis();
    white-space: break-spaces;
    color: $tip_box_text_single_color;
    font-size: $tip_box_text_single_font_size;
    font-weight: $tip_box_text_single_font_weight;
    margin: $tip_box_text_single_margin;
    .text_single_clause {
      color: $tip_box_text_single_clause_color;
      font-size: $tip_box_text_single_clause_font_size;
      font-weight: $tip_box_text_single_clause_font_weight;
    }
  }
  .divider_wrap {
    margin: $tip_box_divider_text_wrap_margin;
    .line {
      height: 1px;
      background-color: $tip_box_divider_line_color;
      @include setFlex(1);
    }
    .divider_text {
      margin: $tip_box_divider_text_margin;
      color: $tip_box_divider_text_color;
      font-size: $tip_box_divider_text_font_size;
      font-weight: $tip_box_divider_text_font_weight;
    }
  }
  .tripartite_wrap {
    width: $tip_box_tripartite_wrap_width;
    margin: $tip_box_tripartite_wrap_margin;
    @include setBox(row, center, center, wrap);
    gap: $tip_box_tripartite_wrap_gap;
    .tool_icon {
      width: $tip_box_tripartite_icon_width;
      height: $tip_box_tripartite_icon_height;
      border: thin solid $tip_box_tripartite_icon_border_color;
      background: $login_google_ban_bg;
      color: $tip_box_tripartite_icon_color;
      border-radius: $tip_box_tripartite_icon_radius;
      @include setBox(row, center, center);
      cursor: pointer;
    }
  }
}
.tripartite_dialog {
  .tripartite_box {
    width: $tip_box_tripartite_dialog_app_width;
    position: fixed;
    top: $tip_box_tripartite_dialog_position_top;
    left: 50%;
    transform: translateX(-50%);
    @include setBox(column, center, center);
    .tripartite_box_title {
      font-size: $tip_box_tripartite_dialog_title_font_size;
      font-weight: $tip_box_tripartite_dialog_title_font_weight;
      padding: $tip_box_tripartite_dialog_title_padding;
      position: relative;
      .close_icon {
        position: absolute;
        right: $tip_box_tripartite_dialog_close_postion_right;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    .tripartite_box_body {
      padding: $tip_box_tripartite_dialog_body_padding;
      .tripartite_box_body_text {
        font-size: $tip_box_tripartite_dialog_body_text_font_size;
        font-weight: $tip_box_tripartite_dialog_body_text_font_weight;
        color: $tip_box_tripartite_dialog_body_text_color;
        margin: $tip_box_tripartite_dialog_body_text_margin;
      }
      .tripartite_box_body_btn {
        margin: $tip_box_tripartite_dialog_body_btn_margin;
        .tripartite_box_body_btn_text {
          font-size: $tip_box_tripartite_dialog_body_btn_font_size;
          font-weight: $tip_box_tripartite_dialog_body_btn_font_weight;
          margin: $tip_box_tripartite_dialog_body_btn_font_margin;
        }
      }
    }
  }
  .tripartite_pop {
    overflow: hidden;
    background: $tip_box_tripartite_dialog_bg;
  }
}

// 针对pc样式
@include breakpoint-only-up() {
  .comp.tip_box {
    .tripartite_wrap {
      .tool_icon {
        &:hover {
          border: $tip_box_tripartite_icon_pc_border;
        }
      }
    }
  }
  .tripartite_dialog {
    .tripartite_box {
      width: $tip_box_tripartite_dialog_pc_width;
    }
  }
}
</style>
