<!--
    phone-form: 填充电话明细
    mail-form: 填充邮箱明细
    forget-pwd-modal: 忘记密码
    reset-pwd-modal: 重置密码
-->
<template>
  <div class="login_modal_box" :class="isApp ? 'login_modal_box_app' : 'login_modal_box_pc'">
    <div class="login_modal_box_pc_left" v-show="!isApp">
      <img class="login_logo_img" :src="loadIcon('login_banner', true)" alt="" />
    </div>
    <div class="login_box">
      <simple-tab-btn
        :is-icon="isIcon"
        :value="tabIndex"
        :fontSize="commonStyle('login_model_simple_tab_font_size')"
        :list="btnTabs"
        @change="changeTab"
        @close="closeDialog"
      />
      <div class="login_box_container">
        <template v-if="isTabShow">
          <div v-if="tabs.length > 1" class="btn_tab_wrap">
            <btn-tab
              :width="commonStyle('login_model_simple_tab_container_btn_tab_width')"
              :radius="commonStyle('login_model_simple_tab_container_btn_tab_radius')"
              :tag-list="tabs"
              :type="btnIndex"
              @changeTab="switchFormTab"
            />
          </div>
        </template>
        <login-regist
          class="login_info"
          :is-login="tabIndex == 0"
          :is-tel="btnIndex == 0"
          :isCpfVerify="isCpfVerify"
          @OnClickCpfVerify="OnClickCpfVerify"
          @close="closeDialog"
          @loginSuccess="loginSuccess"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import SimpleTabBtn from '@/components/ui/simple-tab-btn'
import btnTab from '@/components/ui/btn-tab/index'
import loginRegist from '@/components/login/login-regist'
import loadIcon from '@/assets/icons'
import _common from '@style/components/game-container.login.modules.scss'

// 登录注册后端开关字段
const TYPE_1 = {
  0: 'login_weight_conf',
  1: 'reg_login_weight_conf'
}
export default {
  components: {
    SimpleTabBtn,
    btnTab,
    loginRegist
  },
  props: {
    isIcon: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      // btnTabs: [], // 登录注册
      tabIndex: 0, // 登录注册 0 登录、1 注册
      btnIndex: 0, // 登陆首选项 0 手机号、1 邮箱
      // tabs: [], // 手机号与邮箱tab
      tabsName: {
        email: this.$t('UI_LOGIN_Mail'),
        mobile: this.$t('UI_LOGIN_Phone')
      },
      isCpfVerify: false
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('modal', ['loginRegistStates']),
    ...mapState('base', ['isApp', 'systemConfig']),
    ...mapGetters('base', ['loginSwitch', 'regSwitch']),
    isTabShow() {
      // reg_cpf_control_switch == 1 是cpf注册， reg_cpf_control_switch == 2 加纳，目前没有只有web4才有
      // isCpfVerify cpf 验证成功后才可以现在手机号和邮件
      return !(this.systemConfig.reg_cpf_control_switch == 1 && this.tabIndex != 0) || this.isCpfVerify
    },
    // 登录注册 tab
    btnTabs() {
      const tabs = []
      // 登录显示
      if (this.loginSwitch) {
        tabs.push(this.$t('UI_Login'))
      }
      // 注册显示
      if (this.regSwitch) {
        tabs.push(this.$t('UI_Sign_Up'))
      }
      return tabs
    },
    tabs({ tabIndex }) {
      let currentTab = []
      // 登录
      if (tabIndex === 0) {
        currentTab = this.systemConfig?.login_weight_conf || []
      } else {
        currentTab = this.systemConfig?.reg_login_weight_conf || []
      }
      return currentTab.reduce((pre, cur) => {
        if (cur.status == 1) {
          pre.push({
            key: cur.key,
            name: this.tabsName[cur.key],
            value: cur.key == 'email' ? 1 : 0
          })
        }
        return pre
      }, [])
    }
  },
  watch: {
    // 当前登录注册弹窗是登录还是注册
    loginRegistStates: {
      handler(e) {
        this.tabIndex = e ? 0 : 1
      },
      immediate: true
    }
  },
  mounted() {
    let tabArr = this.systemConfig?.reg_login_weight_conf || []
    tabArr = tabArr.sort((a, b) => b.weight - a.weight)
    this.btnIndex = this.tabs[0].key == 'email' ? 1 : 0
  },
  methods: {
    loginSuccess() {
      this.$emit('loginSuccess')
    },
    loadIcon: type => loadIcon(type, false),
    commonStyle: (k = '') => _common[k],
    // 切换登录注册
    changeTab(index) {
      this.tabIndex = index
      this.isCpfVerify = false
      this.$store.commit('modal/SET_LOGIN_REGIST_STATES', !index)

      // 1. 切换的时候同时也要更新 手机、邮箱的tabs
      // 登录注册在切换的时候，要判断首选项是邮箱 或者 手机号
      let tabArr = this.systemConfig?.[TYPE_1[index]] || []
      tabArr = tabArr
        .sort((a, b) => b.weight - a.weight)
        .filter(v => v.status == 1)
        ?.map(v => ({ name: v.key, value: v.key == 'email' ? 1 : 0 }))

      // this.tabs = tabArr
      this.btnIndex = tabArr[0].value
    },
    // 切换手机号还是邮箱
    switchFormTab({ item = {} }) {
      const { value = 0 } = item
      this.btnIndex = value
    },
    // 关闭弹窗
    closeDialog() {
      this.$store.commit('modal/SET_LOGIN_REGIST_SHOW', false)
    },
    // cpf 判断
    OnClickCpfVerify(type) {
      this.isCpfVerify = type
    }
  }
}
</script>

<style scoped lang="scss">
@import '@style/components/game-container.login.modules.scss';

.login_modal_box {
  width: 100%;
  height: 100%;
  @include setBox(row, flex-start, center);
  &_app {
    width: 100%;
    height: 100%;
  }
  &_pc {
    width: 722px;
    height: $login_modal_pc_height;
    border-radius: 8px;
    overflow: hidden;
  }
  .login_modal_box_pc_left {
    flex-shrink: 0;
    flex-grow: 0;
    width: $login_modal_pc_left_img_width;
    height: 100%;
    flex-shrink: 0;
    .login_logo_img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .login_box {
    flex-shrink: 1;
    flex-grow: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: $login_model_bg;
    overflow: hidden;
    .login_box_container {
      flex: 1;
      padding: $login_model_simple_tab_container_padding;
      overflow-y: auto;
      .btn_tab_wrap {
        margin: $login_model_simple_tab_container_btn_tab_margin;
      }
      .login_info {
        margin: $login_model_simple_tab_container_login_info_margin;
      }
    }
  }
}
</style>
